// -----------------------------------------------------------------------------
// This file contains all styles related to the button component.
// -----------------------------------------------------------------------------

.btn:link,
.btn:visited{
    outline: none;
    text-decoration: none;
}

.btn{
   width: 14rem;
    height: 3.3rem;
    border: none;
    text-align: center;
    margin-top: 2rem;
    transition: all .2s ease-in;
    border-radius: 2.5rem;
    padding: 4px;
    &--primary{
        background-color: var(--color-orange);
        color: var(--color-white);
        font-size: 1.7rem;
        font-family: var(--font-medium);

    }


}





.btn:hover{
    background-color: var(--color-black);
    color: var(--color-white);
   
}

.btn:focus{
    box-shadow: none;
}



.btn-feedback{
    width: 14rem;
    border-radius: 2.5rem;
    background-color: var(--color-white);
    border: 1px solid var(--color-drak-blue);
    padding: 1rem 3rem;
    color: var(--color-drak-blue);
    margin-top: 2rem;
    display: block;
    font-family: var(--font-regular);
    font-size: 1.3rem;
    text-align: center;
}


.search-icon,
.icon-filter{
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: var(--color-white);
    padding: 8px;
    margin-inline-end: 3rem;
    cursor: pointer;

    .icon-search{
        width: 2.2rem;
        height: 2.2rem;
    }

}

.lang-btn{
    padding: 1rem 2rem;
    min-width: 8.6rem;
    height: auto;
    background-color: #B41932;
    color: var(--color-white);
    border: none;
    border-radius: 2.1rem;
    justify-self: flex-end;
    cursor: pointer;
    font-size: 2rem;
    font-family: var(--font-medium);
     align-self: flex-start;
     margin-left: -1rem;
}

.close-btn{
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 3rem 4rem;
    &::after{
        content: "";
        background-image: url(../img/close-btn.svg);
        width: 3rem;
        height: 3rem;
        background-size: contain;
        background-position: center;
        display: inline-block;
    }
}
