.accordion-container{
    position: relative;
    max-width: 100%;
    height: auto;
  }
  .set{
    position: relative;
    width: 100%;
    height: auto;
  }
  .set > a{
    display: inline-flex;
    text-decoration: none;
    justify-content: space-between;
    align-items: center;
    -webkit-transition:all 0.2s linear;
    -moz-transition:all 0.2s linear;
    transition:all 0.2s linear;
    width: 100%;
    color: var(--color-white);
    font-family: var(--font-book);
    font-size: 2rem;
    margin:1rem 0;
  }

  
  .content{
    display:none;

    ul{
        margin-inline-start: 1rem;
        li > a{
            margin-bottom: 1rem;
            color: var(--color-light-blue);
            font-size:1.6rem;
            display: inline-block;

            &:hover{
                color: var(--color-orange);
            }
        }
    }
  }

  .plus-icon{
    &::after{
        content: "+";
        display: inline-block;
        font-size: 2rem;

    }
}

.minus-icon{
    &::after{
        content: "-";
        display: inline-block;
        font-size: 2rem;
    }
}

  
