.popup-slide{
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left:0;
    z-index: 1022;
    background-color: rgba(0, 0, 0, 0.705);
    display: none;
    
}

.menu{
    width: 49.3rem;
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;
    background-color: var(--color-drak-blue);

    &__list{
        padding: 5rem 4rem;
    }

    &__list-normal{
        li > a{
            color: var(--color-white);
            font-size: 2rem;
            color: var(--color-white);
            margin-bottom: 1rem;
            margin-top: 1rem;
            display: inline-block;
        }
    }

    &__header{
        min-height: 24.6rem;
        display: flex;
        &__start{
            width: 38.6rem;
            background-color: var(--color-orange);
            color: var(--color-white);
            align-self: stretch;
            border-radius: 0 0 12.5rem 0;
            padding: 7rem 4rem;
            display: flex;
            align-items: center;
        
           
            .user-name{
                font-size: 2.2rem;
                font-family: var(--font-regular);
                display: block;
            }
            .login-btn{
                cursor: pointer;
                display: inline-block;
                
            }
        }

        &__end{
            display: flex;
            justify-content:space-between;
            flex-direction: column;
            align-items: center;
        }
    }
}

.user-image{
    width: 9rem;
    height: 9rem;
    border-radius:50%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-inline-end: 2.2rem;
    border: 2px solid var(--color-light-blue);

    .icon-guest{
        width: 4.8rem;
        height: 4.8rem;
        
    }

    .user-img{
        @extend%object-fit;
        object-position: top center;
        display: none;
    }
}


