// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------

  
.header{
  background-color: var(--color-black);
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 9rem;
  padding: 0 4rem;
  position: fixed;
  z-index: 99;
  width: 100%;
  top: 0;

  &__middle{
      .region-title{
        display: inline-flex;
        align-items: center;
        text-transform: uppercase;
        cursor: pointer;
        .icon{
          width: 9px;
          height: 18px;
          stroke: var(--color-orange);
          margin-inline-end: 12px;
        }

        span{
          font-size: 2.4rem;
          font-family: var(--font-regular);
          color: var(--color-orange);
          letter-spacing: 3px;
          display: inline-block;
          line-height: 1;
        }
      }
  }

  &__end{
    display: inline-flex;
    align-items: center;
  }
}


.menu-icon{
  cursor: pointer;
}
